import { defineComponent, ref, useContext, watch } from '@nuxtjs/composition-api';
import moment from 'moment';
export default defineComponent({
    props: {
        wAttr: {
            type: Object,
            required: true,
            default: () => ({})
        },
        defaultValue: {
            type: String,
            default: ''
        }
    },
    setup(props, { emit }) {
        const { i18n } = useContext();
        const datePicker = ref(false);
        const menu = ref(null);
        const date = ref('');
        const dateField = ref('');
        watch(() => props.defaultValue, () => {
            date.value = props.defaultValue;
            dateField.value = props.defaultValue;
        }, { immediate: true });
        watch(() => date.value, () => {
            try {
                dateField.value = date.value;
                emit('data', date.value);
            }
            catch (e) {
                console.log(e);
            }
        });
        const onChangeDate = () => {
            if (moment(dateField.value).isValid()) {
                date.value = moment(dateField.value).format('YYYY-MM-DD');
            }
            else {
                date.value = null;
            }
        };
        const setDateNow = () => {
            const dateNow = moment().format('YYYY-MM-DD');
            date.value = dateNow;
            dateField.value = dateNow;
        };
        const dateFieldRules = [
            (v) => !!v || i18n.t('required field'),
            (v) => moment(v).isValid() || i18n.t('invalid date')
        ];
        return {
            datePicker,
            menu,
            props,
            date,
            dateField,
            onChangeDate,
            dateFieldRules,
            setDateNow
        };
    }
});
